<template>
    <table class="web-table-vechicleChecker" cellpadding="0" cellspacing="0">
      <thead>
        <th>类别</th>
        <th width="100">流水号</th>
        <th>检查项目</th>
        <th>判定</th>
      </thead>
      <tbody>
        <tr v-for="item in data" :key="item.id">
            <td v-if="item.order === 1" :rowspan="9">{{ item.type }}</td>
            <td v-if="item.order === 10" :rowspan="5">{{ item.type }}</td>
            <td v-if="item.order === 15" :rowspan="5">{{ item.type }}</td>
            <td v-if="item.order === 20 || item.order === 21">{{ item.type }}</td>
            <td>{{ item.order }}</td>
            <td>{{ item.checkItem }}</td>
            <td>
              <el-select :cAttr="item.attr" ref="trNode"
                v-model="item.value"
                @change="optionDetermine"
                placeholder="请选择"
              >
                <el-option
                  v-for="i in item.determine"
                  :key="i.value"
                  :label="i.label"
                  :value="i.value"
                ></el-option>
              </el-select>
            </td>
        </tr>
      </tbody>
    </table>
</template>
<script>
export default {
    name: 'DthTable',
    props: ['data', 'optionDetermine']   
}
</script>
<style lang="scss" scoped>
.web-table-vechicleChecker {
    width: 50%;
    margin: 0 auto;
    border-collapse: collapse;
    thead > th,
    tr > td{
      border: 1px solid #dedede;
      padding: 10px;
    }
  }
</style>